import React, { Suspense } from 'react';
import ReactLogo from './components/img/react.cd2ab268.svg';
import JsLogo from './components/img/javascript.svg';
import SassLogo from './components/img/sass-1.svg';
import PhpLogo from './components/img/php.svg';
import GitLogo from './components/img/Git_icon.svg';
import CsLogo from './components/img/C_Sharp_logo.svg';
import SQLServerLogo from './components/img/sql_server_logo.svg';
import JavaLogo from './components/img/java_logo.svg';

import '../node_modules/uikit/dist/css/uikit.min.css';
import '../node_modules/uikit/dist/js/uikit-icons.js';

const ParticlesJS = React.lazy(() => import('./components/ParticlesJS/ParticlesJS'));
const Header = React.lazy(() => import('./components/Header/Header'));
const AboutMe = React.lazy(() => import('./components/AboutMe/AboutMe'));
const Habilities = React.lazy(() => import('./components/Habilities/Habilities'));
const Knowledges = React.lazy(() => import('./components/Knowledges/Knowledges'));
const Proyects = React.lazy(() => import('./components/Projects/Projects'));
const Contact = React.lazy(() => import('./components/Contact/Contact'));
const Footer = React.lazy(() => import('./components/Footer/Footer'));

function App() {
  return (
    <div>
      <Suspense fallback={
        <div className="uk-section uk-text-center uk-section-default" style={{width: "100vw", height: "100vh", position: "fixed", zIndex: "999", top: "0", left: "0"}}>
          <div className="uk-container uk-container-xlarge uk-margin-xlarge-top">
            <span data-uk-spinner="ratio: 9;" className="custom-color"></span>
          </div>
        </div>
        }>
        <ParticlesJS CsLogo={CsLogo} JavaLogo={JavaLogo} JsLogo={JsLogo} SQLServerLogo={SQLServerLogo} GitLogo={GitLogo}/>
        <Header/>
        <AboutMe/>
        <Habilities/>
        <Knowledges PhpLogo={PhpLogo} ReactLogo={ReactLogo} SassLogo={SassLogo}/>
        <Proyects/>
        <Contact/>
        <Footer/>
      </Suspense>
    </div>
  );
}

export default App;
